/*  SLIDER RANGE START */

.sliderrange {
  -webkit-appearance: none;
  width: 100%;
  height: 15px;
  border-radius: 5px;
  background: #85929e;
  outline: none;
  opacity: 1;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}

.sliderrange::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #e67e22;
  border: #fefefe;
  border-width: 4px;
  opacity: 1;
  cursor: pointer;
}

input[type="range"]:disabled::-webkit-slider-thumb  {
  background: #A04000 !important;
}
input[type="range"]:disabled  {
  background: #34495E !important;
}

.sliderrange::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #ebdef0;
  opacity: 1;
  cursor: pointer;
}

